import React from 'react'
import { v4 as uuidv4 } from 'uuid'
import { encryptdata } from '../../utility/context/SecurityTool'
import {
  Card,
  FormText,
  CardHeader,
  CardTitle,
  CardBody,
  FormGroup,
  Row,
  Col,
  Input,
  Form,
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  TabContent,
  TabPane,
  Label
} from 'reactstrap'
import Swiper from 'react-id-swiper'
import classnames from 'classnames'
import 'swiper/css/swiper.css'
import AssetAccordion from '../Portfolio/AssetAccordion'
import options from './LiabilitiesOption'
import '../../assets/scss/plugins/extensions/swiper.scss'
import img1 from '../../assets/img/slider/banner-31.jpg'
import img2 from '../../assets/img/slider/banner-22.jpg'
import img3 from '../../assets/img/slider/banner-23.jpg'
import img4 from '../../assets/img/slider/banner-24.jpg'
import img5 from '../../assets/img/slider/banner-25.jpg'
import axios from 'axios'
import { connect } from 'react-redux'
import { toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import { ToastContainer } from 'react-toastify'
import '../../assets/scss/plugins/extensions/toastr.scss'
import '../../assets/scss/plugins/extensions/dropzone.scss'
import { getData, addData } from '../../redux/actions/data-list/'
import Select from 'react-select'
import themeConfig from '../../configs/themeConfig'
import { Delete, Edit, Trash } from 'react-feather'
import '../../assets/scss/pages/data-list.scss'
import '../../assets/scss/components/app-loader.scss'
import Sidebar from '../Nominee/DataListSidebar'
const params = {
  effect: 'cube',
  grabCursor: true,
  autoplay: {
    delay: 2000,
    disableOnInteraction: true
  },
  cubeEffect: {
    shadow: true,
    slideShadows: true,
    shadowOffset: 20,
    shadowScale: 0.94
  },
  pagination: {
    el: '.swiper-pagination'
  }
}

const colourOptions2 = [
  { value: 'Text', label: 'Text', color: '#00B8D9', isFixed: true },
  { value: 'textarea', label: 'Text Area', color: '#0052CC', isFixed: true },
  { value: 'Date', label: 'Date', color: '#0052CC', isFixed: true },
  { value: 'Number', label: 'Number', color: '#0052CC', isFixed: true },
  { value: 'File', label: 'File', color: '#0052CC', isFixed: true }
]

class AddLiability extends React.Component {
  state = {
    messages: this.props.messages,
    open: false,
    deleteID: null,
    fname: '',
    ftype: '',
    liabilityShow: true,
    liabilityList: [],
    liabilityType: '',
    active: '1',
    sidebar: false,
    currentData: null,
    isLoading: false,
    selectAssetRef: React.createRef(),
    selectNomineeRef: React.createRef(),
    selectCustomRef: React.createRef(),
    nomineeOption: [
      {
        value: 'Add Nominee',
        label: 'Add Nominee',
        color: '#00B8D9',
        isFixed: false
      }
    ],
    nominees: [],
    nomineeList: [],
    modal: false,
    selectedTemplate: [],
    isAddDisabled: true,
    template: this.props.messages?.liabilityOption
      ? this.props.messages?.liabilityOption
      : options,
    colourOptions1: [
      {
        value: 'Credit Card',
        label: this.props.messages?.colorOption?.['Credit Card']
          ? this.props.messages?.colorOption?.['Credit Card']
          : 'Credit Card',
        color: '#0052CC',
        isFixed: true
      },
      {
        value: 'Insurance',
        label: this.props.messages?.colorOption?.['Insurance']
          ? this.props.messages?.colorOption?.['Insurance']
          : 'Insurance',
        color: '#0052CC',
        isFixed: true
      },
      {
        value: 'Loan Amount',
        label: this.props.messages?.colorOption?.['Loan Amount']
          ? this.props.messages?.colorOption?.['Loan Amount']
          : 'Loan Amount',
        color: '#00B8D9',
        isFixed: true
      },
      {
        value: 'Notes',
        label: this.props.messages?.colorOption?.['Notes']
          ? this.props.messages?.colorOption?.['Notes']
          : 'Notes',
        color: '#0052CC',
        isFixed: true
      },
      {
        value: 'Ponzi',
        label: this.props.messages?.colorOption?.['Ponzi']
          ? this.props.messages?.colorOption?.['Ponzi']
          : 'Ponzi',
        color: '#0052CC',
        isFixed: true
      },
      {
        value: 'Receipt',
        label: this.props.messages?.colorOption?.['Receipt']
          ? this.props.messages?.colorOption?.['Receipt']
          : 'Receipt',
        color: '#0052CC',
        isFixed: true
      },

      {
        value: 'Warranty',
        label: this.props.messages?.colorOption?.['Warranty']
          ? this.props.messages?.colorOption?.['Warranty']
          : 'Warranty',
        color: '#0052CC',
        isFixed: true
      },
      {
        value: 'Custom',
        label: this.props.messages?.colorOption?.['Custom']
          ? this.props.messages?.colorOption?.['Custom']
          : 'Custom',
        color: '#0052CC',
        isFixed: true
      }
    ]
  }
  componentWillMount() {
    this.getAssets()
  }
  handleSidebar = (boolean, addNew = false) => {
    this.setState({ sidebar: boolean })
    if (addNew === true) this.setState({ currentData: null, addNew: true })
  }
  addData = async (obj) => {
    this.setState({ isLoading: true })
    try {
      await this.props.addData(obj)
      this.setState({ isLoading: false })
      this.handleSidebar(false, true)
    } catch {
      this.setState({ isLoading: false })
      toast.error('Add Nominee Failed! Please contact admin')
    }
  }
  deletecus = (e) => {
    const temp3 = this.state.selectedTemplate.filter((x) => {
      return e.key !== x.key
    })
    this.setState({ selectedTemplate: temp3 })
  }

  getAssets = () => {
    const user = JSON.parse(localStorage.getItem('logInUserData'))
    const { liabilityList } = this.state
    liabilityList.splice(0, liabilityList.length)

    user?.liabilities?.length &&
      user.liabilities.forEach((e, i) => {
        if (e.id) {
          let nomin = ''
          e.nominees.forEach((n, idx) => {
            if (idx === e.nominees.length - 1) {
              nomin = nomin + n.name
            } else {
              nomin = nomin + n.name + ', '
            }
          })
          const cont = (
            <div>
              <Row>
                {e.liabilityDetails.map((x, i) => {
                  if (x.val !== '') {
                    return (
                      <Col md="5" sm="12" key={i + x.key}>
                        <FormText>
                          <b>{x.key} :</b>{' '}
                          {this.state.liabilityShow ? x.val : '***********'}
                        </FormText>
                      </Col>
                    )
                  } else return ''
                })}
                <Col md="5" sm="12">
                  <FormText>
                    <b>Nominees :</b>{' '}
                    {this.state.liabilityShow ? nomin : '*******************'}
                  </FormText>
                </Col>
                <Col md="2" sm="12">
                  <FormText>
                    <b>
                      <Edit
                        className="cursor-pointer mr-1"
                        size={window.screen.width < 600 ? 13 : 20}
                        onClick={() => {
                          this.editItem(e)
                        }}
                      />
                    </b>
                    <b>
                      <Trash
                        className="cursor-pointer"
                        size={window.screen.width < 600 ? 13 : 20}
                        onClick={() => {
                          console.log('event', e)
                          this.setState({ deleteID: e.id, open: true })
                        }}
                      />
                    </b>
                  </FormText>
                </Col>
              </Row>
            </div>
          )
          const ast = {
            id: i + 1,
            title: e.liabilityType,
            content: cont
          }
          liabilityList.push(ast)
        }
      })
    this.setState({ liabilityList: liabilityList?.reverse() })
  }
  static getDerivedStateFromProps(props, state) {
    if (props.dataList.data.length !== state.nomineeList.length) {
      const nomineeOption = [
        {
          value: 'Add Nominee',
          label: 'Add Nominee',
          color: '#00B8D9',
          isFixed: false
        }
      ]
      props.dataList.data.forEach((e) => {
        const temp = {
          value: e._id,
          label: e.name + ' - ' + e.relation,
          color: '#00B8D9',
          isFixed: false
        }
        nomineeOption.push(temp)
      })
      return {
        nomineeList: props.dataList.data,
        nomineeOption: nomineeOption
      }
    }
    return null
  }
  componentDidMount() {
    !this.props.listLoading && this.props.getData()
    this.getAssets()
  }

  toggleModal = () => {
    this.setState((prevState) => ({
      modal: !prevState.modal
    }))
  }

  changeValue = (e, k) => {
    const selectedTemplate = this.state.selectedTemplate
    const idx = selectedTemplate.findIndex((x) => x.key === k.key)
    if (e.length < 50) {
      selectedTemplate[idx].val = e
      this.setState({ selectedTemplate }, this.addAssetsButtonValidation)
    }
  }

  addAssetsButtonValidation = () => {
    let isAddDisabled = false
    this.state.selectedTemplate.forEach((f) => {
      if (f.mandatory && !f.val) {
        isAddDisabled = true
      }
    })
    this.setState({
      isAddDisabled: isAddDisabled
    })
  }

  deleteLiabilty = (id) => {
    const user = JSON.parse(localStorage.getItem('logInUserData'))

    // const as = {
    //   assetType: this.state.assetType,
    //   assetDetails: this.state.selectedTemplate,
    //   nominees: this.state.nominees
    // }

    const idx = user.liabilities.findIndex((el) => el.id === id)
    if (idx >= 0) {
      user.liabilities.splice(idx, 1)
    } else return

    axios
      .post(
        '/backendapi/adddetails',
        {
          _id: user._id,
          liabilities: encryptdata(JSON.stringify(user.liabilities))
        },
        {
          headers: {
            Authorization: 'Bearer ' + localStorage.getItem('authtoken')
          }
        }
      )
      .then((res) => {
        this.props.getData(this.props.parsedFilter)
        this.getAssets()
      })
      .catch()
    localStorage.setItem('logInUserData', JSON.stringify(user))
    toast.warning('Liability deleted successfully!')
    this.clearLiability()
    this.clearCustomField()
    this.setState({ isAddDisabled: true })
  }

  savechanges = (e) => {
    e.preventDefault()
    this.setState({ editItem: false })
    const user = JSON.parse(localStorage.getItem('logInUserData'))
    const as = {
      liabilityType: this.state.liabilityType,
      liabilityDetails: this.state.selectedTemplate,
      nominees: this.state.nominees,
      id: this.state.editId
    }

    const idx = user.liabilities.findIndex((el) => el.id === this.state.editId)
    if (idx >= 0) {
      user.liabilities.splice(idx, 1)
      user.liabilities.push(as)
    } else return

    localStorage.setItem('logInUserData', JSON.stringify(user))

    axios
      .post(
        '/backendapi/adddetails',
        {
          _id: user._id,
          liabilities: encryptdata(JSON.stringify(user.liabilities))
        },
        {
          headers: {
            Authorization: 'Bearer ' + localStorage.getItem('authtoken')
          }
        }
      )
      .then((res) => {
        toast.success('Edited successfully!')
        this.props.getData(this.props.parsedFilter)
        this.getAssets()
      })
      .catch()

    this.clearLiability()
    this.clearCustomField()
    this.setState({ isAddDisabled: true })
  }

  callAddLiability = (e) => {
    e.preventDefault()
    const user = JSON.parse(localStorage.getItem('logInUserData'))
    const as = {
      liabilityType: this.state.liabilityType,
      liabilityDetails: this.state.selectedTemplate,
      nominees: this.state.nominees,
      id: uuidv4()
    }
    user.liabilities.push(as)
    axios
      .post(
        '/backendapi/adddetails',
        {
          _id: user._id,
          liabilities: encryptdata(JSON.stringify(user.liabilities))
        },
        {
          headers: {
            Authorization: 'Bearer ' + localStorage.getItem('authtoken')
          }
        }
      )
      .then((res) => {
        this.props.getData(this.props.parsedFilter)
        this.getAssets()
      })
      .catch()
    localStorage.setItem('logInUserData', JSON.stringify(user))
    toast.success('Liability addedd successfully!')
    this.clearLiability()
    this.clearCustomField()
    this.setState({ isAddDisabled: true })
  }
  addfield = () => {
    if (this.state.liabilityType !== '') {
      const selectedTemplate = this.state.selectedTemplate
      const size = this.state.selectedTemplate.length
      let temp = ''
      if (selectedTemplate[size - 1].key === 'Attachments') {
        temp =
          this.state.selectedTemplate.length &&
          selectedTemplate.splice(this.state.selectedTemplate?.length - 1, 0, {
            key: this.state.fname,
            val: '',
            type: this.state.ftype,
            deletable: true
          })
        this.setState({ temp, fname: '', ftype: '' })
      } else {
        selectedTemplate.push({
          key: this.state.fname,
          val: '',
          type: this.state.ftype,
          deletable: true
        })
        this.setState({ selectedTemplate, fname: '', ftype: '' })
      }
      this.toggleModal()
    }
  }
  changeNominee = (event) => {
    if (this.state.editItem) {
      this.setState({ isAddDisabled: false })
    }
    const nominees = []
    event.length &&
      event.forEach((e) => {
        e.value === 'Add Nominee'
          ? this.setState({ sidebar: true })
          : nominees.push({ id: e.value, name: e.label })
      })
    !event && this.setState({ isAddDisabled: true })
    this.setState({ nominees })
  }

  clearLiability = () => {
    this.state.selectAssetRef.current.select.clearValue()
    this.state.selectNomineeRef.current.select.clearValue()
    this.setState({ liabilityType: '', selectedTemplate: [], nominees: [] })
  }
  clearCustomField = () => {
    this.setState({ fname: '', ftype: '' })
    this.state.selectCustomRef.current &&
      this.state.selectCustomRef.current.select.clearValue()
  }
  handleChange = (e) => {
    this.setState({
      isAddDisabled: true,
      fname: '',
      ftype: '',
      liabilityType: e ? e.value : undefined,
      selectedTemplate: e ? this.state.template[e.value] : {}
    })
  }
  editItem = (item) => {
    console.log('editItem', item)
    const { liabilityDetails, liabilityType, nominees } = item // item.id is left
    this.setState({ liabilityType })
    this.setState({ editItem: true })
    this.setState({ editId: item.id })
    this.setState({ selectedTemplate: liabilityDetails })
    this.setState({ nominees })
  }
  animateLabel = (value) => {
    if (value) {
      document.getElementById('buttonLabel').classList.remove('no-display')
      document.getElementById('buttonLabel').classList.add('button-text')
    } else {
      document.getElementById('buttonLabel').classList.add('no-display')
      document.getElementById('buttonLabel').classList.remove('button-text')
    }
  }

  render() {
    const { messages, thumbView, getData, parsedFilter } = this.props
    const {
      editItem,
      sidebar,
      currentData,
      isLoading,
      addNew,
      active,
      selectAssetRef,
      colourOptions1,
      nomineeOption,
      selectNomineeRef,
      template,
      selectedTemplate,
      nominees,
      isAddDisabled,
      liabilityList,
      liabilityType,
      liabilityShow,
      modal,
      fname,
      selectCustomRef
    } = this.state
    return (
      <div className={`data-list ${thumbView ? 'thumb-view' : 'list-view'}`}>
        <Modal
          isOpen={this.state.open}
          className="modal-dialog-centered"
          backdrop="static"
        >
          <ModalBody>Are you sure you want to delete this record?</ModalBody>
          <ModalFooter>
            <Button
              color="primary"
              onClick={() => this.setState({ open: false })}
              className="button-label"
            >
              Cancel
            </Button>
            <Button
              color="danger"
              onClick={() => {
                this.deleteLiabilty(this.state.deleteID)
                this.setState({ open: false })
              }}
              className="button-label"
            >
              Delete
            </Button>
          </ModalFooter>
        </Modal>
        <Sidebar
          show={sidebar}
          data={currentData}
          updateData={this.updateData}
          addData={this.addData}
          isLoading={isLoading}
          handleSidebar={this.handleSidebar}
          thumbView={thumbView}
          getData={getData}
          dataParams={parsedFilter}
          addNew={addNew}
        />
        <div
          className={classnames('data-list-overlay', {
            show: sidebar
          })}
          onClick={() => this.handleSidebar(false, true)}
        />
        <Row>
          <Col lg="8" md="6" sm="12">
            <div style={{ width: window.screen.width < 500 ? '100%' : '150%' }}>
              <Row>
                <Col sm="12">
                  <TabContent activeTab={active}>
                    <TabPane tabId="1">
                      <Row>
                        <ToastContainer />
                        <Col lg="8" md="6" sm="12">
                          <Card>
                            <CardHeader>
                              <CardTitle>
                                {messages?.subHeading
                                  ? messages.subHeading
                                  : editItem
                                  ? 'Edit Liability Details'
                                  : 'Add New Liability'}
                              </CardTitle>
                            </CardHeader>
                            <CardBody>
                              <Form>
                                <Row>
                                  <Col md="6" sm="12">
                                    <FormGroup className="form-label-group">
                                      <Select
                                        className="React"
                                        classNamePrefix="select"
                                        ref={selectAssetRef}
                                        name="color"
                                        options={colourOptions1}
                                        isClearable={true}
                                        isDisabled={editItem}
                                        value={colourOptions1.filter(
                                          (option) =>
                                            option.value === liabilityType
                                        )}
                                        placeholder={
                                          messages?.liabilityOptionText
                                            ? messages?.liabilityOptionText
                                            : 'Select Liability Type...'
                                        }
                                        onChange={(e) => {
                                          this.handleChange(e)
                                        }}
                                      />
                                      <Label for="nameMulti">
                                        Liability Type
                                      </Label>
                                    </FormGroup>
                                  </Col>
                                  <Col md="6" sm="12">
                                    <FormGroup className="form-label-group">
                                      <Select
                                        isMulti
                                        name="nominee"
                                        options={nomineeOption}
                                        value={nominees.map((n) => {
                                          return { value: n.id, label: n.name }
                                        })}
                                        ref={selectNomineeRef}
                                        className="React"
                                        classNamePrefix="select"
                                        placeholder={
                                          messages?.nomineeOption
                                            ? messages?.nomineeOption
                                            : 'Select Nominees...'
                                        }
                                        id="nominee"
                                        onChange={(e) => {
                                          this.changeNominee(e)
                                        }}
                                      />
                                      <Label>Nominee</Label>
                                    </FormGroup>
                                  </Col>
                                </Row>
                                <Row>
                                  {Object.keys(template).includes(
                                    liabilityType
                                  ) &&
                                    selectedTemplate.map((e, i) => {
                                      return (
                                        <Col md="6" sm="12" key={i}>
                                          <FormGroup className="form-label-group">
                                            {e.deletable && (
                                              <Delete
                                                style={{
                                                  position: 'absolute',
                                                  right: '-5px',
                                                  top: '-7px'
                                                }}
                                                className="primary"
                                                size={15}
                                                onClick={() => {
                                                  this.deletecus(e)
                                                }}
                                              />
                                            )}
                                            <Input
                                              type={e.type}
                                              name="city"
                                              id="cityMulti"
                                              className="input-label"
                                              placeholder={e.key}
                                              value={e.val}
                                              onChange={(ev) => {
                                                this.changeValue(
                                                  ev.target
                                                    ? ev.target.value
                                                    : ev.value,
                                                  e
                                                )
                                              }}
                                            />
                                            <Label
                                              for="cityMulti"
                                              className={
                                                themeConfig.theme === 'dark'
                                                  ? 'dark-label'
                                                  : 'light-label'
                                              }
                                            >
                                              {e.key}
                                            </Label>
                                          </FormGroup>
                                        </Col>
                                      )
                                    })}
                                </Row>
                                <Row style={{ marginTop: '15px' }}>
                                  <Col
                                    md="6"
                                    sm="12"
                                    style={{ display: 'flex' }}
                                  >
                                    {liabilityType && (
                                      <>
                                        <Button
                                          color="white"
                                          outline
                                          onClick={this.toggleModal}
                                          className="add-button"
                                          id="addButton"
                                          onMouseEnter={() =>
                                            this.animateLabel(true)
                                          }
                                          onMouseLeave={() =>
                                            this.animateLabel(false)
                                          }
                                          style={{
                                            backgroundColor: 'var(--warning)'
                                          }}
                                        >
                                          +
                                        </Button>
                                        <div className="label-div">
                                          <span
                                            id="buttonLabel"
                                            className="no-display"
                                          >
                                            Add a Custom Field
                                          </span>
                                        </div>
                                      </>
                                    )}
                                  </Col>
                                  <Col sm="6">
                                    <FormGroup
                                      className="action-right"
                                      style={{
                                        textAlign: 'right',
                                        marginBottom: '0px'
                                      }}
                                    >
                                      <Button.Ripple
                                        outline
                                        color="secondary"
                                        type="reset"
                                        className="button-label"
                                        onClick={(e) => {
                                          this.setState({ editItem: false })
                                          this.clearLiability()
                                        }}
                                      >
                                        {editItem ? 'Cancel' : 'Reset'}
                                      </Button.Ripple>
                                      <Button.Ripple
                                        color="warning"
                                        type="submit"
                                        className="button-label"
                                        disabled={
                                          // editItem
                                          //   ? nominees.length === 0 ||
                                          //     !liabilityType ||
                                          //     !editItem
                                          //   :
                                          nominees.length === 0 ||
                                          !liabilityType ||
                                          !editItem ||
                                          isAddDisabled
                                        }
                                        onClick={(e) => {
                                          editItem
                                            ? this.savechanges(e)
                                            : this.callAddLiability(e)
                                        }}
                                      >
                                        {editItem ? 'Update' : 'Add'}
                                      </Button.Ripple>
                                    </FormGroup>
                                  </Col>
                                </Row>
                              </Form>
                            </CardBody>
                          </Card>
                        </Col>
                      </Row>

                      <Row>
                        <ToastContainer />
                        <Col md="8">
                          <AssetAccordion
                            heading={
                              messages?.subHeading
                                ? messages.subHeading
                                : 'Liabilities List'
                            }
                            collapseItems={liabilityList}
                            show={() => {
                              this.setState({
                                liabilityShow: !liabilityShow
                              })
                              this.getAssets()
                            }}
                            assetShow={liabilityShow}
                          />
                        </Col>
                      </Row>
                    </TabPane>
                  </TabContent>
                </Col>
              </Row>
            </div>
          </Col>
          <Col lg="4" md="6" sm="12">
            <div className="swipermobile">
              <Swiper {...params}>
                <div
                  style={{
                    backgroundImage: `url(${img1})`,
                    backgroundSize: 'contain'
                  }}
                ></div>
                <div
                  style={{
                    backgroundImage: `url(${img2})`,
                    backgroundSize: 'contain'
                  }}
                ></div>
                <div
                  style={{
                    backgroundImage: `url(${img3})`,
                    backgroundSize: 'contain'
                  }}
                ></div>
                <div
                  style={{
                    backgroundImage: `url(${img4})`,
                    backgroundSize: 'contain'
                  }}
                ></div>
                <div
                  style={{
                    backgroundImage: `url(${img5})`,
                    backgroundSize: 'contain'
                  }}
                ></div>
              </Swiper>
            </div>
          </Col>
        </Row>

        <Row>
          {/* To add a */}
          <Col sm="12">
            <Modal
              isOpen={modal}
              toggle={this.toggleModal}
              className="modal-dialog-centered"
            >
              <ModalHeader toggle={this.toggleModal}>
                Add Custom Field
              </ModalHeader>
              <ModalBody>
                <FormGroup className="form-label-group">
                  <Input
                    type="email"
                    name="Email"
                    disabled={liabilityType === ''}
                    id="EmailMulti"
                    value={fname}
                    placeholder="Enter Field Name"
                    onChange={(e) => {
                      this.setState({ fname: e.target.value })
                    }}
                  />
                  <Label for="EmailMulti">Enter Field Name</Label>
                </FormGroup>
                <FormGroup
                  className="form-label-group"
                  disabled={liabilityType === undefined}
                >
                  <Select
                    className="React"
                    ref={selectCustomRef}
                    disabled={liabilityType === undefined}
                    classNamePrefix="select"
                    isClearable={true}
                    name="color"
                    options={colourOptions2}
                    placeholder="Select Field Type..."
                    onChange={(e) =>
                      this.setState({ ftype: e ? e.value : ' ' })
                    }
                  />
                  <Label for="nameMulti">Field Type</Label>
                </FormGroup>
              </ModalBody>
              <ModalFooter>
                <FormGroup className="form-label-group">
                  <Button.Ripple
                    outline
                    color="secondary"
                    type="reset"
                    className="mb-1 button-label"
                    disabled={liabilityType === undefined}
                    onClick={(e) => {
                      this.clearCustomField()
                    }}
                  >
                    Reset
                  </Button.Ripple>{' '}
                  <Button.Ripple
                    color="warning"
                    disabled={liabilityType === undefined}
                    type="submit"
                    className="button-label"
                    onClick={(e) => this.addfield()}
                  >
                    {'Add'}
                  </Button.Ripple>
                </FormGroup>
              </ModalFooter>
            </Modal>
          </Col>
        </Row>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    dataList: state.dataList,
    listLoading: state.dataList.totalRecordsLoading
  }
}

export default connect(mapStateToProps, { getData, addData })(AddLiability)
