import React, { Component } from 'react'
import { Label, Input, FormGroup, Button } from 'reactstrap'
import { X } from 'react-feather'
import PerfectScrollbar from 'react-perfect-scrollbar'
// import { Spinner } from 'reactstrap'
import classnames from 'classnames'
import themeConfig from '../../configs/themeConfig'

class DataListSidebar extends Component {
  state = {
    isLoading: this.props.isLoading,
    relation: 'Collegue',
    relation1: 'Collegue',
    email: '',
    address: '',
    contact1: '',
    contact2: '',
    id: '',
    name: '',
    errors: {
      relation: false,
      email: false,
      address: false,
      contact1: false,
      contact2: false,
      name: false
    }
  }

  addNew = false

  componentDidUpdate(prevProps, prevState) {
    console.log('ADD NEW NOMINEE', this.props.isLoading)
    if (this.props.data !== null && prevProps.data === null) {
      if (this.props.data.id !== prevState.id) {
        this.setState({ id: this.props.data.id })
      }
      if (this.props.data.name !== prevState.name) {
        this.setState({ name: this.props.data.name })
      }
      if (this.props.data.relation !== prevState.relation) {
        this.setState({ relation: this.props.data.relation })
      }
      if (this.props.data.relation !== prevState.relation1) {
        this.setState({ relation1: this.props.data.relation })
      }
      if (this.props.data.address !== prevState.address) {
        this.setState({ address: this.props.data.address })
      }
      if (this.props.data.email !== prevState.email) {
        this.setState({ email: this.props.data.email })
      }
      if (this.props.data.primaryContact !== prevState.contact1) {
        this.setState({ contact1: this.props.data.primaryContact })
      }
      if (this.props.data.secondaryContact !== prevState.contact2) {
        this.setState({ contact2: this.props.data.secondaryContact })
      }
    }
    if (this.props.data === null && prevProps.data !== null) {
      this.setState({
        id: '',
        name: '',
        relation: '',
        relation1: '',
        email: '',
        address: '',
        contact1: '',
        contact2: ''
      })
    }
    if (this.addNew) {
      this.setState({
        id: '',
        name: '',
        relation: '',
        relation1: '',
        email: '',
        address: '',
        contact1: '',
        contact2: ''
      })
    }
    this.addNew = false
  }

  handleValidation = (type) => {
    const { relation, relation1, email, address, contact1, contact2, name } =
      this.state

    let errors = this.state.errors

    switch (type) {
      case 'name':
        if (name.length === 0 && name === '') {
          console.log(name.length, name)
          errors.name = true
        } else {
          console.log(name.length, name)
          errors.name = false
        }
        break
      case 'email':
        if (email === '') {
          errors.email = true
        } else if (email !== '') {
          if (typeof email !== 'undefined') {
            if (
              !email.match(
                /^([A-Za-z0-9_\-.])+@([A-Za-z0-9_\-.])+\.([A-Za-z]{2,})$/
              )
            ) {
              errors.email = true
            } else {
              errors.email = false
            }
          }
        }
        break
      case 'address':
        if (!address) {
          errors.address = true
        } else {
          errors.address = false
        }
        break
      case 'contact1':
        if (contact1.length === 0) {
          errors.contact1 = true
        } else if (contact1.length !== 0) {
          if (typeof contact1 !== 'undefined') {
            // if (
            //   !contact1.match(
            //     /^\+-((?:9[679]|8[035789]|6[789]|5[90]|42|3[578]|2[1-689])|9[0-58]|8[1246]|6[0-6]|5[1-8]|4[013-9]|3[0-469]|2[70]|7|1)(?:\W*\d){0,13}\d$/
            //   )
            // ) {
            //   errors.contact1 = true
            // }
            if (contact1.length !== 10) {
              errors.contact1 = true
            } else {
              errors.contact1 = false
            }
          }
        }
        break
      case 'contact2':
        if (contact2.length === 0) {
          errors.contact2 = false
        } else if (contact2.length !== 0 && contact2.length === 10) {
          errors.contact2 = false
        } else {
          errors.contact2 = true
        }
        break
      case 'relation':
        if (relation === 'others' && relation1.length === 0) {
          errors.relation = true
        } else {
          errors.relation = false
        }
        break
      default:
        break
    }

    this.setState({ errors: errors })
  }

  handleValue = (type, value) => {
    switch (type) {
      case 'name':
        this.setState({ name: value?.trim() })
        break
      case 'email':
        this.setState({ email: value?.trim() })
        break
      case 'address':
        this.setState({ address: value?.trim().replace(/[\n\r]/g, ', ') })
        break
      case 'contact1':
        this.setState({ contact1: value })
        break
      case 'contact2':
        this.setState({ contact2: value })
        break
      case 'relation':
        this.setState({ relation: value?.trim() })
        this.setState({ relation1: value?.trim() })
        break
      case 'relation1':
        this.setState({ relation1: value?.trim() })
        break
      default:
        break
    }
  }

  resetError = (type) => {
    let errors = this.state.errors
    switch (type) {
      case 'name':
        errors.name = false
        break
      case 'email':
        errors.email = false
        break
      case 'address':
        errors.address = false
        break
      case 'contact1':
        errors.contact1 = false
        break
      case 'contact2':
        errors.contact2 = false
        break
      case 'relation':
        errors.relation = false
        break
      default:
        break
    }

    this.setState({ errors: errors })
  }

  handleSubmit = (obj) => {
    const { relation, relation1, email, address, contact1, contact2, name } =
      this.state
    const { errors } = this.state

    if (name.length === 0 && name === '') {
      console.log(name.length, name)
      errors.name = true
    } else {
      console.log(name.length, name)
      errors.name = false
    }

    if (email === '') {
      errors.email = true
    } else if (email !== '') {
      if (typeof email !== 'undefined') {
        if (
          !email.match(
            /^([A-Za-z0-9_\-.])+@([A-Za-z0-9_\-.])+\.([A-Za-z]{2,})$/
          )
        ) {
          errors.email = true
        } else {
          errors.email = false
        }
      }
    }

    if (!address) {
      errors.address = true
    } else {
      errors.address = false
    }

    if (contact1.length === 0) {
      errors.contact1 = true
    } else if (contact1.length !== 0) {
      if (typeof contact1 !== 'undefined') {
        // if (
        //   !contact1.match(
        //     /^\+-((?:9[679]|8[035789]|6[789]|5[90]|42|3[578]|2[1-689])|9[0-58]|8[1246]|6[0-6]|5[1-8]|4[013-9]|3[0-469]|2[70]|7|1)(?:\W*\d){0,13}\d$/
        //   )
        // ) {
        //   errors.contact1 = true
        // }
        if (contact1.length !== 10) {
          errors.contact1 = true
        } else {
          errors.contact1 = false
        }
      }
    }

    if (contact2.length === 0) {
      errors.contact2 = false
    } else if (contact2.length !== 0 && contact2.length === 10) {
      errors.contact2 = false
    } else {
      errors.contact2 = true
    }

    if (relation === 'others' && relation1.length === 0) {
      errors.relation = true
    } else {
      errors.relation = false
    }

    if (
      !(
        errors.name ||
        errors.address ||
        errors.contact1 ||
        errors.contact2 ||
        errors.email ||
        errors.relation
      )
    ) {
      if (this.props.data !== null) {
        obj._id = this.props.data._id
        obj.usage = this.props.data.usage
        this.props.updateData(obj)
      } else {
        this.addNew = true
        this.props.addData(obj)
      }
    }

    this.setState({ errors: errors })
  }

  nullData = () => {
    this.setState({
      name: '',
      relation: '',
      relation1: '',
      email: '',
      address: '',
      contact1: '',
      contact2: ''
    })
  }

  render() {
    let error = this.state.errors
    let { show, handleSidebar, data } = this.props
    // const { errors } = this.state
    let { name, relation, img } = this.state
    return (
      <div
        className={classnames('data-list-sidebar', {
          show: show
        })}
      >
        <div className="data-list-sidebar-header mt-2 px-2 d-flex justify-content-between">
          <h4 style={{ color: 'var(--warning)' }}>
            {data !== null ? 'UPDATE NOMINEE' : 'ADD NEW NOMINEE'}
          </h4>
          <X
            size={20}
            onClick={() => {
              handleSidebar(false, true)
              this.nullData()
            }}
          />
        </div>
        <PerfectScrollbar
          className="data-list-fields px-2 pt-2"
          options={{ wheelPropagation: false }}
        >
          <FormGroup className="form-label-group mb-2">
            <Input
              className="input-label"
              type="text"
              value={name}
              placeholder="Nominee Name"
              onChange={(e) => {
                this.handleValue('name', e.target.value)
                this.resetError('name')
              }}
              onBlur={() => this.handleValidation('name')}
              id="data-name"
              style={{ borderColor: error.name ? 'red' : '' }}
            />
            <Label
              className={
                themeConfig.theme === 'dark' ? 'dark-label' : 'light-label'
              }
              for="data-name"
            >
              Name
            </Label>
          </FormGroup>
          <FormGroup>
            <Input
              type="select"
              id="data-category"
              value={relation}
              onChange={(e) => {
                this.handleValue('relation', e.target.value)
              }}
            >
              <option>Collegue</option>
              <option>Cousin</option>
              <option>Daughter</option>
              <option>Daughter-in-law</option>
              <option>Friend</option>
              <option>Husband</option>
              <option>Son</option>
              <option>Son-in-law</option>
              <option>Wife</option>
              <option>others</option>
            </Input>
            <Label
              className={
                themeConfig.theme === 'dark'
                  ? 'dark-label select-label'
                  : 'light-label select-label'
              }
              for="data-category"
            >
              Relation
            </Label>
          </FormGroup>
          {relation === 'others' && (
            <FormGroup className="form-label-group mb-2">
              <Input
                type="text"
                value={this.state.relation1}
                placeholder="Relation Name"
                onChange={(e) => {
                  this.handleValue('relation1', e.target.value)
                  this.resetError('relation')
                }}
                onBlur={() => this.handleValidation('relation')}
                id="data-Relation"
                style={{ borderColor: error.relation ? 'red' : '' }}
              />
            </FormGroup>
          )}
          <FormGroup className="form-label-group mb-2">
            <Input
              className="input-label"
              type="email"
              value={this.state.email}
              placeholder="Enter nominee email"
              onChange={(e) => {
                this.handleValue('email', e.target.value)
                this.resetError('email')
              }}
              onBlur={() => this.handleValidation('email')}
              id="data-email"
              style={{ borderColor: error.email ? 'red' : '' }}
            />
            <Label
              className={
                themeConfig.theme === 'dark' ? 'dark-label' : 'light-label'
              }
              for="data-email"
            >
              Nominee Email
            </Label>
          </FormGroup>
          <FormGroup className="form-label-group mb-2">
            <Input
              className="input-label"
              type="number"
              value={this.state.contact1}
              onChange={(e) => {
                this.handleValue('contact1', e.target.value)
                this.resetError('contact1')
              }}
              onBlur={() => this.handleValidation('contact1')}
              id="data-price"
              placeholder="Enter primary contact No"
              style={{ borderColor: error.contact1 ? 'red' : '' }}
            />
            <Label
              className={
                themeConfig.theme === 'dark' ? 'dark-label' : 'light-label'
              }
              for="data-price"
            >
              Primary Contact
            </Label>
          </FormGroup>
          <FormGroup className="form-label-group mb-2">
            <Input
              className="input-label"
              type="number"
              value={this.state.contact2}
              onChange={(e) => {
                this.handleValue('contact2', e.target.value)
                this.resetError('contact2')
              }}
              onBlur={() => this.handleValidation('contact2')}
              id="data-price"
              placeholder="Enter secondry contact No"
              style={{ borderColor: error.contact2 ? 'red' : '' }}
            />
            <Label
              className={
                themeConfig.theme === 'dark' ? 'dark-label' : 'light-label'
              }
              for="data-price"
            >
              Secondary Contact
            </Label>
          </FormGroup>
          <FormGroup className="form-label-group mb-2">
            <Input
              className="input-label "
              style={{
                height: '115px',
                borderColor: error.address ? 'red' : ''
              }}
              type="textarea"
              value={this.state.address}
              placeholder="Address"
              onChange={(e) => {
                this.handleValue('address', e.target.value)
                this.resetError('address')
              }}
              onBlur={() => this.handleValidation('address')}
              id="data-Address"
            />
            <Label
              className={
                themeConfig.theme === 'dark' ? 'dark-label' : 'light-label'
              }
              for="data-Address"
            >
              Address *
            </Label>
          </FormGroup>

          {this.props.thumbView && img.length <= 0 ? (
            <label
              className="btn btn-primary"
              htmlFor="upload-image"
              color="primary"
            >
              Upload Image
              <input
                type="file"
                id="upload-image"
                hidden
                onChange={(e) =>
                  this.setState({ img: URL.createObjectURL(e.target.files[0]) })
                }
              />
            </label>
          ) : null}
        </PerfectScrollbar>
        <div className="data-list-sidebar-footer px-2 d-flex justify-content-start align-items-center mt-1">
          <Button.Ripple
            className="button-label"
            color="secondary"
            outline
            onClick={() => {
              // handleSidebar(false, true)
              this.nullData()
            }}
          >
            Reset
          </Button.Ripple>
          <Button.Ripple
            //disabled
            color="warning"
            type="reset"
            className="button-label"
            onClick={() => this.handleSubmit(this.state)}
          >
            {/* {this.props.isLoading && <Spinner color="danger" size="sm" />} */}
            {data !== null
              ? this.props.isLoading
                ? 'Updating...'
                : 'Update'
              : this.props.isLoading
              ? 'Adding...'
              : 'Add'}
          </Button.Ripple>
        </div>
      </div>
    )
  }
}
export default DataListSidebar
